<template>
  <div class="desktop fx-pac">
    <router-view>
    </router-view>
    <ChatRoom v-if="appMap['ChatRoom'].isLoad"></ChatRoom>
    <Brower v-if="appMap['Brower'].isLoad"></Brower>
    <Live2DShow v-if="appMap['Live2DShow'].isLoad"></Live2DShow>
    <Lyre v-if="appMap['Lyre'].isLoad"></Lyre>
    <Clipboard v-if="appMap['Clipboard'].isLoad"></Clipboard>
    <Video v-if="appMap['Video'].isLoad"></Video>
    <FileList v-if="appMap['FileList'].isLoad"></FileList>
    <AMap v-if="appMap['AMap'].isLoad"></AMap>

    <div class="shortcutBox fx-ver fx-bm">
      <div class="fx-ver fx-ac shortcut" v-for="(it,idx) of appMap" :key="idx" @click="startApp(it.name)">
        <span class="fz34">{{ it.icon }}</span>
        <span class="fz14">{{ it.CName }}</span>
      </div>
      <div class="fx-ver fx-ac shortcut" v-if="isMobile" @click="goMobilePage">
        <span class="fz34">üì≤</span>
        <span class="fz14">ÂéªÁßªÂä®Á´Ø</span>
      </div>
    </div>
    <div class="footer" style="z-index: 999;">
      <div class="start" v-if="openStart">
        <div class="start-select" @click="about">
          <span>ÂÖ≥‰∫éÊú¨Á´ô</span><span class="fa fa-smile-o fz22"></span>
        </div>
      </div>
      <span class="system fa fa-windows fz22" title="ÂºÄÂßã" @click="openStart=!openStart"></span>
      <span v-for="(item,idx) in runningApp" :key="idx" class="fz22"
            @click="restore(item)">{{ appMap[item].icon }}</span>
    </div>
  </div>
</template>

<script>
import ChatRoom from "@/components/ChatRoom/ChatRoom";
import Brower from "@/components/Brower/Brower";
import Live2DShow from "@/components/Live2DShow/Live2DShow";
import Lyre from "@/components/Lyre/Lyre";
import Clipboard from "@/components/Clipboard/Clipboard";
import Video from "@/components/Video/Video";
import pageManager from "@/utils/PageManager";
import FileList from "@/components/FileList/FileList";
import AMap from "@/components/AMap/AMap";

export default {
  name: "Desktop",
  components: {FileList, ChatRoom, Brower, Live2DShow, Lyre, Clipboard, Video, AMap},
  computed: {
    runningApp() {
      let tmp = [];
      for (let item in this.appMap) {
        if (this.appMap[item].isLoad)
          tmp.push(item);
      }
      return tmp;
    }
  },
  created() {
    this.isMobile = window.browser.versions.mobile;
  },
  mounted() {
    document.body.addEventListener('mousemove', ev => {
      pageManager.mousemove(ev);
    });
    document.body.addEventListener('mouseup', ev => {
      pageManager.mouseup(ev);
    });
    document.addEventListener('keydown', ev => {
      pageManager.keydown(ev);
    });
  },
  data() {
    return {
      appMap: pageManager.getAppMap(),
      minAppMap: pageManager.getMinAppMap(),
      openStart: false,
      isMobile: false
    }
  },
  methods: {
    goMobilePage() {
      this.$router.push("/mobile");
    },
    dynamicLoadJs(url, callback) {
      let head = document.getElementsByTagName('head')[0];
      let script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = url;
      if (typeof (callback) == 'function') {
        script.onload = script.onreadystatechange = function () {
          if (!this.readyState || this.readyState === "loaded" || this.readyState === "complete") {
            callback();
            script.onload = script.onreadystatechange = null;
          }
        };
      }
      head.appendChild(script);
    },
    restore(name) {
      pageManager.restore(this.appMap[name]);
    },
    startApp(name) {
      pageManager.startApp(name);
    },
    about() {
      this.$message.info("ÊöÇÊó∂Ê≤°ÂÜôÔºÅüåù")
    }
  }
}
</script>

